import { Component, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { Funciones } from '../compartido/funciones';
import { environment as env } from 'src/environments/environment';
import { ApiMongoService } from '../services/apiMongo.service';
import * as $ from 'jquery';
import { IonSlides, ModalController } from '@ionic/angular';
import { DetailAttrComponent } from '../components/detail-attr/detail-attr.component';
import { ProcesoComponent } from '../components/proceso/proceso.component';
import { AlamcenComponent } from '../components/alamcen/alamcen.component';
import { StorageService } from '../services/storage.service';
import { IonicComponentsService } from '../services/ionic-components.service';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage {

  constructor(
    public _comp: IonicComponentsService,
    private _storage: StorageService,
    private _modal: ModalController,
    private _fun: Funciones,
    private _apiMongo: ApiMongoService,
    private fun: Funciones,
    public router: Router) {
    this.getTrace();
/*     this.getUser(); */
  }

  slideOpts = {
    initialSlide: 0,
    slidesPerView: 1,
    speed: 700,
    autoplay: {
      delay: 10000,
    },
  };

  user: any;
  page = 'info';

  //COMPONENT INFO LOTE
  trace: any = {
    credentialSubject: {
      details: [],
      header: {
        attributes: {
          country_source: "",
          country_source_key: "",
          country_target: "",
          country_target_key: "",
          delivery_date: null,
          notes: "",
          order_date: null,
          product_type: "",
          product_type_key: "",
          quantity: 0,
          reference_key: "",
          sku: "",
          unit: "",
          via_type: "",
          via_type_key: "",
        }
      }
    }
  }

  //COMPONENT TRAZABILIDAD
  traceDetail = [];

  //COMPONET ALAMCEN
  producersLot = [];

  ionViewDidEnter() {
    this.getUser();
  }

  async getUser() {
    this.user = await this._storage.getLocalStorage('USER');
  }

  async getTrace() {
    
    let loading: any = await this._comp.presentLoading();
    let codeLote = await this._storage.getLocalStorage('CODE');
    
    if (this._fun.isVarInvalid(codeLote)) {
      loading.dismiss();
      this.router.navigate(['/notfound']);
      return;
    }



    try {
      let res_trace: any = await this._apiMongo.getTrace(codeLote);
      if (res_trace.code == 401) {
        loading.dismiss();
        this.router.navigate(['/notfound']);
        return;
      }

      //VALIDAR QUE CUADNO SE MANDA UN CODIGO INVALIDO MANDAR A LA PAGINA NOT FOUND

      this.trace = res_trace;
      

      for (let index = 0; index < this.trace.credentialSubject.details.length; index++) {
        const trace_detail = this.trace.credentialSubject.details[index];

        if (trace_detail.code == 'ALMACEN') this.usersAlmacen(trace_detail);
        else if (trace_detail.code != 'salida_puerto' && trace_detail.code != 'delivery' && trace_detail.code != 'liq_plant') this.traceDetail.push(trace_detail);

        if (index == (this.trace.credentialSubject.details.length - 1)) {
          this.traceDetail = this._fun.sortJSON(this.traceDetail, 'start', true);
          loading.dismiss();
        }
      }

    } catch (error) {
      loading.dismiss();
      await this._fun.alertError(error);
    }
  }

  async permisoLogin(page) {

    if (this.user == 'INVITADO') {
      let res = await this.fun.initSesion();
      if (this.fun.isVarInvalid(res)) return;
      await this._storage.remove('USER');
      this.router.navigate(['/login']);
    } else this.page = page;

  }

  async usersAlmacen(alm) {
    this.producersLot = alm.attributes;
    

return;
    for (let index = 0; index < this.producersLot.length; index++) {
      const prod = this.producersLot[index];
      let dateNow = Math.floor(new Date().getTime() / 1000);

      let res: any = await this._apiMongo
        .filter(env.COLLECTION.document,
          env.TABLE_SIS.external,
          {
            "data.document.subject.data.dids.address": prod.values[0].id,
            // "data.document.stamp_db": { "$lte": Math.floor(alm.start / 1000) },//INICIO DE PARTIDA
            "data.document.iat": { "$lte": dateNow },
            "data.document.exp": { "$gt": dateNow }

          });
      
      if (this._fun.isEmpty(res.result)) this.producersLot[index].certs = [];
      else {
        this.producersLot[index].certs = res.result[0].external;
        
      }


    }
  }

  async procesos() {
    const modal = await this._modal.create({
      /*       cssClass: 'style-info', */
      component: ProcesoComponent,
      componentProps: {
        proceso: this.traceDetail
      }
    });

    modal.onDidDismiss().then(async (res: any) => {

      

    });
    return await modal.present();
  }

}
